
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

export default {
name: "topic",
props: ["id"],
components: {
Group,
Cell,
XButton,
ButtonTab,
ButtonTabItem
},
data() {
return {
list: [],
market: [],
lists: [],
sid: null
};
},
methods: {
djsj: function(id) {
var app = this;
this.$http
.get("/v2-car-getInfoAndListById.html?SerialID=" + to.params.id)
.then(function(response) {
for (var i = 0; i < response.data.data.list.length; i++) {
var listest = [];
if (response.data.data.list[i].market_attribute == "2018") {
listest.push(response.data.data.list[i]);
}
}
app.lists = listest;
});
},
djsj1: function(id) {
var app = this;
this.$http
.get("/v2-car-getInfoAndListById.html?SerialID=" + to.params.id)
.then(function(response) {
for (var i = 0; i < response.data.data.list.length; i++) {
var listest = [];
if (response.data.data.list[i].market_attribute == "2018") {
listest.push(response.data.data.list[i]);
}
}
app.lists = listest;
});
},
djsj2: function(id) {
var app = this;
this.$http
.get("/v2-car-getInfoAndListById.html?SerialID=" + to.params.id)
.then(function(response) {
for (var i = 0; i < response.data.data.list.length; i++) {
var listest = [];
if (response.data.data.list[i].market_attribute == "2018") {
listest.push(response.data.data.list[i]);
}
}
app.lists = listest;
});
}
},
beforeRouteEnter: function(to, from, next) {
next(function(vm) {
vm.$http
.get("/v2-car-getInfoAndListById.html?SerialID=" + to.params.id)
.then(function(response) {



vm.market = response.data.data.market_attribute;
vm.list = response.data.data.list;
vm.lists = response.data.data;
vm.sid = response.data.data.SerialID;
});
});
}
};



